.skills {
    grid-area: skills;
    height: 100%;
    border-left: .2rem solid var(--purple-light);
    padding: 1rem 2rem 1rem 2rem;
}

.skills-wrapper {
    height: 100%;
    position: relative;
    z-index: 2;
}

.icons-wrapper {
    position: relative;
    height: 80%;

}

.icons-user {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: flex-start;

}

.icon-selection {
    position: absolute;
    bottom: -10rem;
    right: 0rem;
    height: 10rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: flex-start;
    background-color: var(--white);
    border: 1px solid var(--purple-light);
    border-radius: .5rem;
    box-shadow: 0px .5rem 10px rgba(0, 0, 0, .2);
    padding: 1rem 2rem 1rem 2rem;
    transition: all 0.15s ease-out;
    z-index: 2;

}

.icon-container {
    position: relative;
}

.icon {
    height: 3rem;
    color: var(--purple-dark);
    transition: all .15s ease-out;
}

.icon:hover {
    color: var(--purple);
    transform: scale(1.05);
}

.icon-container button {
    position: absolute;
    right: -1rem;
    border: none;
    background-color: var(--red);
    color: var(--white);
    height: 1.8rem;
    width: 1.8rem;
    border-radius: 500px;
}

.close-modal {
    position: absolute;
    background-color: rgba(255, 255, 255, 0);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
}

.icon-selection button {
    position: absolute;
    top: .3rem;
    right: .3rem;
}